html {
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__logo {
  align-self: center;
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  margin: 0 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item:first-child .nav__link {
  margin-left: 0;
}

.nav__item:last-child .nav__link {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: block;
}

.card__details {
  margin-bottom: 16px;
}

.card__title {
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__description {
  color: #616070;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  margin-top: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews-text {
  color: #060b35;
  align-self: baseline;
  margin: 0;
  line-height: 16px;
  display: inline-block;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-label {
  color: #616070;
  margin: 0;
  font-size: 12px;
}

.card__price-value {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
}

.card__link {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.stars {
  gap: 4px;
  padding: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  box-sizing: border-box;
  grid-template-columns: repeat(1, 200px);
  justify-content: space-between;
  gap: 46px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 1200px) {
  .container {
    padding-inline: 128px;
  }
}
